/**
 * Welcome model events
 */

'use strict';

import {EventEmitter} from 'events';
import Welcome from './welcome.model';
var WelcomeEvents = new EventEmitter();

// Set max event listeners (0 == unlimited)
WelcomeEvents.setMaxListeners(0);

// Model events
var events = {
  save: 'save',
  remove: 'remove'
};

// Register the event emitter to the model events
for(var e in events) {
  let event = events[e];
  Welcome.schema.post(e, emitEvent(event));
}

function emitEvent(event) {
  return function(doc) {
    WelcomeEvents.emit(event + ':' + doc._id, doc);
    WelcomeEvents.emit(event, doc);
  };
}

export default WelcomeEvents;
